<template>
    <aside class="aside">
        <h3 class="aside__title">Открытый ключ, <br> полученный с сервера:</h3>
        <span class="aside__key"> {{ key }} </span>
    </aside>
</template>

<script>
export default {
  name: 'Aside',
  data(){
      return {
        serverResponse: {},
      }
  },
  mounted(){
    this.getKey();
  },
  methods: {
        getKey(){
                this.serverResponse = {
                    openKey: 12222,
                    privateKey: 1225222,
                }

                this.$store.commit({
                    type: 'setKey',
                    openKey: this.serverResponse.openKey,
                    privateKey: this.serverResponse.privateKey,
                });
        }
  },
  computed: {
    key() {
      return `(${this.serverResponse.openKey}, ${this.serverResponse.privateKey})`;
    }
  },
}
</script>

