<template>
    <Header/>
    <main class="main confirm">
        <div class="container confirm__container">
            <div class="confirm__body">
                <h2 class="confirm__title">Спасибо! <br> Ваш голос учтен.</h2>
                <p class="confirm__subtitle">Вы проголосовали "<span class="confirm__subtitle-accent">{{ voteText }}</span>"</p>
                <a :href="href" class="confirm__return">
                    Вернуться на страницу университета
                    <svg width="41" height="16" viewBox="0 0 41 16" fill="none" xmlns="http://www.w3.org/2000/svg">
                        <path d="M40.7071 8.70711C41.0976 8.31658 41.0976 7.68342 40.7071 7.29289L34.3431 0.928932C33.9526 0.538408 33.3195 0.538408 32.9289 0.928932C32.5384 1.31946 32.5384 1.95262 32.9289 2.34315L38.5858 8L32.9289 13.6569C32.5384 14.0474 32.5384 14.6805 32.9289 15.0711C33.3195 15.4616 33.9526 15.4616 34.3431 15.0711L40.7071 8.70711ZM0 9H40V7H0V9Z"/>
                    </svg>
                </a>
            </div>
        </div>
    </main>
</template>

<script>

import Header from './Header';

export default {
    name: 'Confirm',
    components: {
        Header,
    },
    data(){
        return {
            href: 'https://www.sut.ru/'
        }
    },
    mounted(){
        if((localStorage.user && !Object.prototype.hasOwnProperty.call(JSON.parse(localStorage.getItem('user')), "name")) || (localStorage.getItem('user') == null)){
            this.$router.push({
                path: '/registration',
                
            });
        }

        if(!localStorage.getItem('vote')){
            this.$router.push({
                path: '/registration',
            });
        }

        this.$store.commit({
            type: 'setVote',
            vote: localStorage.getItem('vote'),
        })
    },
    computed: {
        voteText(){
            return this.$store.getters.vote;
        }
    },
}
</script>

