<template>
    <a v-if="href" :href="href" class="logo">
        <img src="../assets/logo.png" alt="Логотип" class="logo__img">
    </a>
    <a v-else @click.prevent="resetData()" class="logo">
        <img src="../assets/logo.png" alt="Логотип" class="logo__img">
    </a>
</template>

<script>
export default {
  name: 'Logo',
  data: () => {
      return {
          href: ''
      }
  },
  methods: {
      resetData(){
        localStorage.removeItem('vote');
        localStorage.removeItem('user');

        this.$router.push({
            path: '/registration',
        });
      }
  }
}

</script>

