import { createApp } from 'vue'
import { createStore } from 'vuex'
import { createWebHashHistory, createRouter } from 'vue-router'
import App from './App.vue'


import './assets/sass/style.sass'

import Registration from './components/Registration'
import Main from './components/Main'
import Confirm from './components/Confirm'

const routes = [
    {
        path: '/registration',
        alias: '/',
        component: Registration,
    },
    {
        path: '/main',
        component: Main,
    },
    {
        path: '/confirm',
        component: Confirm,
    },
];

const store = createStore({
    state () {
      return {
        isConfirmedMember: false,
        vote: '',
        userData: {
            name: '',
            surname: '',
        },
        key: {
            openKey: undefined,
            privateKey: undefined,
        }
      }
    },
    getters: {
        isConfirmedMember: state => {
            return state.isConfirmedMember;
        },
        userData: state => {
            return state.userData;
        },
        vote: state => {
            return state.vote;
        },
        key: state => {
            return state.key;
        }
    },
    mutations: {
        memberConfirmation(state){
            state.isConfirmedMember = true;
        },
        setVote(state, payload){
            state.vote = payload.vote;

            localStorage.setItem('vote', payload.vote);
        },
        setKey(state, payload){
            state.key = {
                openKey: payload.openKey,
                privateKey: payload.privateKey,
            }
        },
        setUserData(state, payload){
            state.userData = {
                name: payload.name,
                surname: payload.surname,
            }

            localStorage.user = JSON.stringify({
                name: payload.name,
                surname: payload.surname,
            });
        }
    }
});

const router = createRouter({
    mode: 'history',
    routes,
    history: createWebHashHistory(),
})
  
const app =  createApp(App);
app.use(router);
app.use(store);
app.mount('#app')
