<template>
    <Header/>
    <main class="main">
        <div class="container main__container">
            <div class="main__body">
                <div class="main__head">
                    <h2 class="main__title">Бюллетень</h2>
                    <p class="main__text">{{ meetengData }}</p>
                    <p class="main__text">{{ protocol }}</p>
                </div>
                <div class="main__table table">
                    <div class="table__item table__item_head">
                        <span class="table__text">
                            Фамилия, имя, <br> отчество соискателя
                        </span>
                    </div>
                    <div class="table__item table__item_head">
                        <span class="table__text">
                            Достоин ученой степени
                        </span>
                    </div>
                    <div class="table__item table__item_head">
                        <span class="table__text">
                            Результаты <br> голосования
                        </span>
                    </div>
                    <div class="table__item table__item_data">
                        <span class="table__text">
                            {{ candidatName }}
                        </span>
                    </div>
                    <div class="table__item table__item_data">
                        <span class="table__text">
                            {{ candidatGrade }}
                        </span>
                    </div>
                    <div class="table__item table__item_data">
                        <button class="button button_orange table__button" @click="voteButtonClick" data-content="Да">Да</button>
                        <button class="button button_black table__button" @click="voteButtonClick" data-content="Нет">Нет</button>
                    </div>
                </div>
            </div>
            <Aside/>
        </div>
    </main>

    <div class="popup" :class="{active: popups.confirmPopup.isVisible}" ref="confirmPopup" @click="closePopupByBackground(this.popups.confirmPopup)">
        <div class="popup__body">
            <h2 class="popup__title">Ваш голос: "<span class="popup__title popup__title_accent">{{ votedText }}</span>"</h2>
            <div class="popup__footer">
                <button class="button button_orange popup__button" @click="showResultPopup()">Подтвердить</button>
                <button class="button button_black popup__button" @click="closePopup(this.popups.confirmPopup)">Отмена</button>
            </div>
        </div>
    </div>

    <div class="popup popup_underheader" :class="{active: popups.enterPopup.isVisible}" ref="enterPopup">
        <div class="popup__body">
            <h2 class="popup__title">Подтвердите участие <br> в голосовании</h2>
            <div class="popup__footer">
                <button class="button button_orange popup__button" @click="confirmMember()">Подтвердить</button>
            </div>
        </div>
    </div>

    <div class="popup" :class="{active: popups.resultPopup.isVisible}" ref="resultPopup">
        <div class="popup__body">
            <h2 class="popup__title popup__title_result">Результат шфирования:</h2>
            <p class="popup__subtitle">{{ result }}</p>
            <div class="popup__footer">
                <button class="button button_orange popup__button" @click="confirm()">Ок</button>
            </div>
        </div>
    </div>
</template>

<script>

import Header from './Header';
import Aside from './Aside';

export default {
    name: 'Main',
    data(){
        return {
            meetengData: 'Диссертационный совет Д 219.004.04',
            protocol: 'К заседанию совета 09 декабря 2020 года, протокол №11',
            candidatName: 'Иванов Иван Иванович',
            candidatGrade: 'Кандидат технических наук',
            votedText: '',
            popups: {
                confirmPopup: {
                    name: 'confirmPopup',
                    isVisible: false,
                },
                enterPopup: {
                    name: 'enterPopup',
                    isVisible: false,
                },
                resultPopup: {
                    name: 'resultPopup',
                    isVisible: false,
                },
            },
            result: 0,
        }
    },
    components: {
        Header,
        Aside,
    },
    mounted(){
        if((localStorage.user && !Object.prototype.hasOwnProperty.call(JSON.parse(localStorage.getItem('user')), "name")) || (localStorage.getItem('user') == 'null')){
            this.$router.push({
                path: '/registration',
            });
        }

        if(localStorage.getItem('vote') != 'null'){
            this.$router.push({
                path: '/confirm',
            });
        }

        this.openPopup(this.popups.enterPopup);
    },
    computed: {
        key(){
            return this.$store.getters.key
        },
        votersCount(){
            return 32;
        },
        voterIndex(){
            return 2;
        }
    },
    methods: {
        pow(x, n){
            x = typeof x != 'bigint' ? BigInt(x) : x;
            n = typeof n != 'bigint' ? BigInt(n) : n;

            if (n==0n)
                return 1n;
            else if (n==1n)
                return x;
            else if (n % 2n == 0n )
                return this.pow( x * x, n / 2n);
            else
                return this.pow( x * x, n / 2n) * x;
        },
        voteButtonClick(){
            const button = event.target;

            this.votedText = button.dataset?.content;
            
            this.openPopup(this.popups.confirmPopup);
        },
        getRandomInt(min, max) {
            min = Math.ceil(min);
            max = Math.floor(max);

            return Math.floor(Math.random() * (max - min)) + min;
        },
        openPopup(popup){
            popup.isVisible = true;
        },
        closePopupByBackground(popup){
            if(event.target.isEqualNode(this.$refs.confirmPopup)){
                this.closePopup(popup);
            }
        },
        closePopup(popup){
            popup.isVisible = false;
        },
        closeAllPopups(){
            for(let popup in this.popups){
                this.popups[popup].isVisible = false;
            }
        },
        cypherAnswer(){
            const n = this.key.openKey;
            const g = this.key.privateKey;

            const voteState = this.votedText == 'Да' ? 1 : 2;

            const m = this.pow(this.votersCount, 0) + this.pow(this.votersCount, voteState);

            const random = this.getRandomInt(2, n);

            this.result = this.pow(g, m) * this.pow(random, n) % this.pow(n, 2);
        },
        showResultPopup(){

            this.cypherAnswer();

            this.closePopup(this.popups.confirmPopup)
            this.openPopup(this.popups.resultPopup);
        },
        confirm(){
            this.$store.commit({
                type: 'setVote',
                vote: this.votedText,
            });

            this.$router.push({
                path: '/confirm',
                
            });
        },
        confirmMember(){
            this.closePopup(this.popups.enterPopup);
            this.$store.commit('memberConfirmation');
        },
    }
}

</script>

